body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.idx {
  display: flex;
  flex-direction: column;
}
/* Contact Info */

.contactInfo {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.contactInfo>div {
    margin: 10px;
}

.thumbnail {
    width: 24px;
    -webkit-filter: invert(25%);
            filter: invert(25%);
    transition-duration: .4s;
}

.thumbnail:hover {
    -webkit-filter: invert(5%);
            filter: invert(5%);
}
:root {
  --dark: #506d6e;
  --med: #255491;
  --light: #C2F9BB;
  --link: #62C370;

  /* IE and Edge */
  -ms-overflow-style: none;
  /* Firefox */
  scrollbar-width: none;
}

html::-webkit-scrollbar {
  display: none;
}

h1 {
  margin: 20px 0px;
  padding-left: 10px;
  padding-right: 10px;
  color: #C2F9BB;
  color: var(--light);
  text-align: center;
}

h2 {
  margin: 10px 0px;
  padding-bottom: 16px;
  padding-top: 16px;
  padding-left: 10px;
  padding-right: 10px;
  color: #C2F9BB;
  color: var(--light);
  background-color: #255491;
  background-color: var(--med);
  text-align: center;
}

h3 {
  margin: 6px;
  padding-left: 10px;
  padding-right: 10px;
  color: #C2F9BB;
  color: var(--light);
  text-align: center;
}

p {
  padding: 10px;
  margin: 0px;
  color: #C2F9BB;
  color: var(--light);
}

.text-link {
  text-decoration: none;
  color: #62C370;
  color: var(--link)
}

.text-link:hover {
  text-decoration: underline;
}

.main-text {
  padding: 10px 100px;
}

@media screen and (max-width: 800px) {
  .main-text {
    padding: 10px
  }
}

.App {
  display: flex;
  flex-direction: column;
  color: #C2F9BB;
  color: var(--light);
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  padding: 0px;
}

.App-nav {
  background-color: #255491;
  background-color: var(--med);
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  width: 100%;
  overflow: hidden;
}

.App-header {
  display: flex;
  flex-direction: row;
  padding: 0px;
  text-align: center;
  /* background-color: var(--med); */
  width: 100%;
}


.App-section {
  padding: 0px;
  margin: 10px 0px;
  width: -webkit-fill-available;
  width: stretch;
  display: flex;
  flex-direction: column;
  color: #C2F9BB;
  color: var(--light);
}

.App-work {
  display: flex;
  flex-direction: row;
  overflow: auto;
  padding: 20px 50px;

}

.App-work::-webkit-scrollbar {
  display: none;
}

.App-link {
  color: #62C370;
  color: var(--link);
}

.App-anchor {
  position: relative;
  top: -70px;
}

.App-footer {
  height: auto
}

.hr {
  align-self: stretch;
  /* background-image: linear-gradient(to right, var(--med), var(--dark), var(--med)); */
  background-color: #506d6e;
  background-color: var(--dark);
  height: 2px;
}

.vr {
  padding: 0px;
  margin: 0px;
  align-self: stretch;
  background-image: linear-gradient(to right, #255491, #506d6e, #255491);
  background-image: linear-gradient(to right, var(--med), var(--dark), var(--med));
  width: 4px;
}

.imageStyle {
  height: 200px;
  width: auto;
}

.imageRowStyle {
  display: flex;
  flex-direction: row;
  padding: 0px 10px;
  width: 100vm;
  justify-content: center;
  flex-wrap: wrap;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content
}

.fancybg {
  background-image: linear-gradient(to right, #506d6e, #255491, #506d6e);
  background-image: linear-gradient(to right, var(--dark), var(--med), var(--dark));
}

.deep {
  box-shadow: inset 0px 0px 10px 5px #506d6e;
  box-shadow: inset 0px 0px 10px 5px var(--dark);
}

.deep-y {
  /* background: linear-gradient(to top, var(--dark), transparent 10px),
    linear-gradient(to bottom, var(--dark), transparent 10px); */
}

.outset {
  box-shadow: 0px 0px 5px 3px #506d6e;
  box-shadow: 0px 0px 5px 3px var(--dark)
}

.c {
  text-align: center;
}

.solid-bg {
  background-color: #255491;
  background-color: var(--med);
}

#background-video {
  width: 108vw;
  height: calc(100vh + 8vw);
  object-fit: cover;
  position: fixed;
  top: -4vw;
  left: -4vw;
  z-index: -1;
  -webkit-filter: none;
          filter: none
}
.navBar {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    padding: 10px;
    margin: 0px;
    width: -webkit-fill-available;
    width: stretch;
    height: 50px;
}

.logo {
    width: 50px;
    border-radius: 50%;
    margin: 10px;
}

.title {
    margin: 10px;
    margin-right: auto;
    padding: 0;
}

.menu {
    display: flex;
    flex-direction: row;
    list-style-type: none;
}

.menuItem {
    width: 60px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    padding: 10px;
    margin: 10px;
    background-color: teal;
    border-radius: 3px;
    color: var(--light);
    text-decoration: none;
    text-align: center;
    scroll-behavior: smooth;
    cursor: pointer;
    transition-duration: .4s;
}

.menuItem:hover {
    background-color: var(--light);
    color: teal;
}

.menuItem:active {
    background-color: white;
}

.menuItem1 {
    width: 60px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    padding: 10px;
    margin: 10px;
    border-style: solid;
    border-color: teal;
    border-radius: 3px;
    color: var(--light);
    text-decoration: none;
    text-align: center;
    transition-duration: .4s;
    cursor: pointer;
}

.menuItem1:hover {
    background-color: teal;
}



.selected {
    background-color: rgb(0, 185, 185);
}
.projectCard {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 200px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    margin: 10px;
    text-align: center;
    text-decoration: none;
    color: var(--light);
    background-color: var(--med);
    border-style: solid;
    border-radius: 10px;
    border-width: 2px;
    border-color: var(--dark);
    overflow: hidden;
    transition-duration: .4s;
}

.projectCard:hover {
    box-shadow: -5px 0px 0px 0px var(--dark);
}
